import siteinfo from '@/common/siteinfo';
export default {
	data() {
		return {

			stars: 5,
			pic1: '',
			pic2: '',
			pic3: '',

			plimg1: '/static/photo_icon.png',
			plimg2: '/static/photo_icon.png',
			plimg3: '/static/photo_icon.png',

			lbpic: 0,
			lbpath: [],

			wordnum: 0,
			niming: 2,
			options: '',
		}
	},
	onLoad(options) {
		var that = this;
		that.options = options;
		// this.$http.post("goods/getonegoods",{id: 7448}).then((res)=>{
		// 	that.goodsdata = res.data
		// })

	},
	methods: {
		choosestar(e) {
			var that = this;
			var stars = e.currentTarget.id;

			that.stars = stars;
		},
		changeswitch(e) {
			var that = this;
			if (e.detail.value == true) {
				var val = 1;
			} else {
				var val = 2;
			}
			that.niming = val;
		},
		textinput(e) {
			var len = e.detail.value.length;
			this.wordnum = len;
		},
		tianjia(e) {
			var that = this;
			var body = encodeURI(e.detail.value.body);

			if (body == '' || !body) {
				uni.showModal({
					title: '温馨提示',
					content: '请输入评论内容',
					showCancel: false,
				})
				return;
			}
			that.addFormID(e.detail.formId);
			that.$http.post("comment/addcomment", {
				goods_id: that.options.goods_id,
				order_id: that.options.order_id,
				niming: that.niming,
				stars: that.stars,
				froms: that.options.froms ? that.options.froms : 'shop'
			}, {
				headers: {
					'content-type': 'application/x-www-form-urlencoded',
					'body': body,
				}
			}).then((res) => {
				console.log(res);
				if (res.code == 1) {
					if (that.lbpic < 1) {
						uni.showToast({
							title: '发布成功',
							success: function() {
								setTimeout(function() {
									uni.navigateBack({})
								}, 1500)
							}
						})
					} else {
						that.uploadimg(res.data, 1, 'new')
					}


				}
			})

		},
		// 上传图片
		uploadimg(id, i, add) {
			var that = this;
			if (i == 1) {
				var filepath = that.plimg1;
			} else if (i == 2) {
				var filepath = that.plimg2;
			} else if (i == 3) {
				var filepath = that.plimg3;
			}
			var token = uni.getStorageSync('token');
			console.log("请求地址", siteinfo.websiteUrl + "comment/uploadimg");
			console.log("图片地址", filepath);
			uni.uploadFile({
				url: siteinfo.websiteUrl + "comment/uploadimg",
				filePath: filepath,
				name: "file",
				formData: {
					token: token,
					types: 'image',
					id: id,
					froms: that.options.froms ? that.options.froms : 'shop',
				},
				success: function(res) {
					if (i == that.lbpic && res.data) {
						uni.hideLoading();
						uni.showToast({
							title: '发表完成',
							duration: 1500,
						})
						setTimeout(function() {
							console.log('返回2')
							uni.navigateBack({
								delta: 1,
							})
						}, 1500)

					} else {
						that.uploadimg(id, i = i + 1, 'old');
					}

				}
			});
		},

		// 选择图片
		chooseimg: function(e) {
			var that = this;

			uni.chooseImage({
				count: 1,
				success: function(res) {
					console.log(res)
					if (e.target.id == 'pic1') {
						console.log(1)
						var num = that.lbpic;

						var index = num - 1;
						if (that.plimg1 != '/static/photo_icon.png') {

							that.plimg1 = res.tempFilePaths[0];
							that.lbpath[index] = res.tempFilePaths[0];

						} else {

							that.plimg1 = res.tempFilePaths[0];
							that.lbpic = that.lbpic + 1;
							that.lbpath[num] = res.tempFilePaths[0];

						}

					} else if (e.target.id == 'pic2') {
						console.log(2)
						var num = that.lbpic;
						var index = num - 1;
						if (that.plimg2 != '/static/photo_icon.png') {

							that.plimg2 = res.tempFilePaths[0];
							that.lbpath[index] = res.tempFilePaths[0];

						} else {

							that.plimg2 = res.tempFilePaths[0];
							that.lbpic = that.lbpic + 1;
							that.lbpath[num] = res.tempFilePaths[0];

						}
					} else if (e.target.id == 'pic3') {
						console.log(3)
						var num = that.lbpic
						var index = num - 1;
						if (that.plimg3 != '/static/photo_icon.png') {

							that.plimg3 = res.tempFilePaths[0];
							that.lbpath[index] = res.tempFilePaths[0];

						} else {

							that.plimg3 = res.tempFilePaths[0];
							that.lbpic = that.lbpic + 1;
							that.lbpath[num] = res.tempFilePaths[0];

						}
					} else {
						console.log(4)
					}
					console.log('图片数组')
					console.log(that.lbpath);
				},
			})
		},
		// 删除图片
		delarrimg: function(res) {
			var that = this;
			console.log('删除图片');
			var index = res.currentTarget.dataset.id;
			if (index == 0 || index == 1 || index == 2) {
				var array = that.lbpath;
				array.splice(index, 1);
				var numname = 'lbpic';
			} else {
				var array = that.sppath;
				array.splice(index, 1);
				var numname = 'sppic';
			}
			var num = index * 1 + 1;

			that[numname] = that[numname] - 1;
			// console.log(that.)
			that['plimg' + num] = '/static/photo_icon.png';

		},

	}
}
